$font-100: "M PLUS 1", sans-serif;
$text-100: #202124;
$text-200: #4f68f1;
$text-300: #585b63;
$text-400: #fcfbfb;
$text-500: #ffffff;
$surface-100: #ffffff;
$surface-200: #4f68f1;
$surface-300: #2641da;
$border-100: #d2d3d7;
$border-200: #4f68f1;

form {
 display: flex;
 flex-direction: column;
 inline-size: 100%;
 margin-block: 3em;

 .form-control {
  display: block;
  position: relative;
  border-radius: .325em;
  inline-size: 100%;
  block-size: 3.5em;
  margin-block-end: 1em;

  input,
  textarea {
   display: block;
   position: absolute;
   block-size: 100%;
   inline-size: 100%;
   padding: .75em .75em .875em;
   letter-spacing: .015em;
   border-radius: inherit;
   font-size: 1em;
   background: $surface-100;
   color: $text-100;
   border: 1.5px solid $border-100;
   

   &:focus {
    border: 1.5px solid $border-200;

    & + label {
     top: -.7em;
     left: .65em;
     font-size: .7em;
     color: $text-200;
    }
   }

   &:not(:placeholder-shown) {
    & + label {
     top: -.7em;
     left: .65em;
     font-size: .7em;
    }
   }
  }

  label {
   position: absolute;
   top: .975em;
   left: .65em;
   z-index: 20;
   padding-inline: .15em;
   font-size: 1em;
   color: $text-300;
   letter-spacing: .015em;
   background: $surface-100;
   transition: all 150ms ease;
  }

  &:last-of-type {
   block-size: 10em;
  }

  @media screen and (min-width: 768px) {
   max-inline-size: 500px;
   inline-size: 100%;
   margin-inline: auto;
  }
 }

 button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: .375em;
  text-align: center;
  margin-inline: auto;
  font-size: 1.25em;
  font-weight: 500;
  line-height: 1.5;
  border-radius: .5em;
  padding: .875em 2.25em;
  font-family: $font-100;
  border: 1px solid $border-200;
  background-color: $surface-200;
  color: $text-400;
  transition: background-color 75ms linear;
  -webkit-font-smoothing: antialiased;

  &:hover {
   background-color: $surface-300;
   color: $text-500;
  }
 }
}