$text-100: #fcfbfb;
$text-200: #f0f1f3;

article {
 display: block;
 padding-inline: .75em;

 .container {
  padding: 2em 1.5em;
  border-radius: 1.5em;

  h3 {
   font-size: 1.5em;
   font-weight: 400;
   letter-spacing: .03em;
   line-height: 1.5;
   color: $text-100;
   text-shadow: 0 1px 0 #000;
   margin-block-end: .5em;
  }

  h4 {
   font-size: 1.125em;
   letter-spacing: .03em;
   color: $text-200;
   font-weight: 400;
   text-shadow: 0 1px 0 #000;
   margin-block-end: .25em;
  }

  h5 {
   font-size: 1em;
   color: $text-200;
   opacity: .85;
   font-weight: 400;
   letter-spacing: .03em;
   text-shadow: 0 1px 0 #000;
  }
 }

 &:nth-of-type(1) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)),url(https://jeffclaybrook.s3.amazonaws.com/conferenceroom.webp) no-repeat 50%/cover;
  }
 }

 &:nth-of-type(2) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)),url(https://jeffclaybrook.s3.amazonaws.com/posed.webp) no-repeat 50%/cover;
  }
 }

 &:nth-of-type(3) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)),url(https://jeffclaybrook.s3.amazonaws.com/generic1.webp) no-repeat 50%/cover;
  }
 }

 &:nth-of-type(4) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)),url(https://jeffclaybrook.s3.amazonaws.com/generic2.webp) no-repeat 50%/cover;
  }
 }

 @media screen and (min-width: 768px) {
  padding-inline: 3.5em;

  .container {
   padding: 3em;
   border-radius: 2.5em;

   h3 {
    font-size: 1.75em;
   }
 
   h4 {
    font-size: 1.25em;
   }
 
   h5 {
    font-size: 1.0875em;
   }
  }
 }
}