$text-100: #202124;
$text-200: #4f68f1;
$surface-100: #f7f9fc;
$surface-200: #536dfe22;

.basic-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 margin-block: 3em;

 .basic-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: .75em;
  background: $surface-100;
  box-shadow: 0 0 0 1px rgba(0,0,0,.05);
  border-radius: .5em;
  padding: 1.5em .75em;
  position: relative;
  text-align: center;
 
  .basic-card-icon {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   block-size: 3.5em;
   inline-size: 3.5em;
   border-radius: 1em;
   padding: .75em;
   background: $surface-200;
 
   svg {
    fill: $text-200;
   }
  }
 
  h4 {
   font-size: 1.25em;
   font-weight: 500;
   letter-spacing: .02em;
   color: $text-100;
   -webkit-font-smoothing: antialiased;
  }
 }

 @media screen and (min-width: 600px) {
  grid-template-columns: repeat(2, 1fr);
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr);
 }
}