$font-100: "M PLUS 1", sans-serif;
$text-100: #202124;
$text-200: #4f68f1;
$text-300: #fcfbfb;
$text-400: #ffffff;
$surface-100: #f7f9fc;
$surface-200: #4f68f1;
$surface-300: #2641da;
$border-100: #4f68f1;

header {
 display: block;
 block-size: calc(100vh - 4em);
 background: $surface-100;

 .container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-inline: .75em;
  block-size: calc(100% - 4em);

  h1 {
   font-size: 2em;
   font-weight: 350;
   letter-spacing: .02em;
   color: $text-100;
   margin-block-end: .0875em;

   strong {
    color: $text-200;
    font-weight: 600;
   }
  }

  h2 {
   font-size: 1.5em;
   font-weight: 350;
   letter-spacing: .015em;
   margin-block-end: 1em;

   strong {
    color: $text-200;
    font-weight: 600;
   }
  }

  a {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   gap: .375em;
   text-align: center;
   color: $text-300;
   font-family: $font-100;
   font-size: 1.25em;
   line-height: 1.5;
   font-weight: 500;
   border-radius: .5em;
   padding: .875em 1.25em;
   border: 1px solid $border-100;
   background-color: $surface-200;
   transition: background-color 75ms linear;
   -webkit-font-smoothing: antialiased;

   &:hover {
    background-color: $surface-300;
    color: $text-400;
   }
  }
 }

 @media screen and (min-width: 768px) {
  .container {
   padding-inline: 3.5em;
  }
 }
}