$text-100: #202124;
$text-200: #4f68f1;
$text-300: #585b63;
$surface-100: #f7f9fc;
$surface-200: #ffffffbf;
$surface-300: #ffffffe6;

section {
 display: block;
 margin-block-end: 4em;
 padding-block-start: 4em;

 .container {
  display: flex;
  flex-direction: column;
  padding-inline: .75em;

  h2 {
   font-size: 2.25em;
   font-weight: 400;
   letter-spacing: .02em;
   text-align: center;
   color: $text-200;
  }

  h3 {
   font-size: 1.25em;
   font-weight: 400;
   letter-spacing: .02em;
   color: $text-100;
   text-align: center;
   padding-inline: .75em;
  }

  .carousel {
   margin-block: 3em;
  
   .splide__arrow:not(:disabled) {
    box-shadow: 0 1px 2px 0 #3c40434d, 0 1px 3px 1px #3c404326;
    background: $surface-200;
    block-size: 3em;
    inline-size: 3em;
    border-radius: 50%;
    opacity: 1;
  
    svg {
     fill: $text-300;
    }
  
    &:hover {
     background: $surface-300;
     opacity: 1;
    }
   }
  
   li {
    border-radius: .75em;
    box-shadow: 0 0 0 1px rgba(0,0,0,.05);
    background: $surface-100;
  
    img {
     display: block;
     object-fit: cover;
     border-radius: inherit;
    }
   }
  }
 }

 @media screen and (min-width: 768px) {
  .container {
   padding-inline: 3.5em;
  }
 }
}