$font-100: "M PLUS 1", sans-serif;
$text-100: #202124;
$text-200: #4f68f1;
$text-300: #585b63;
$surface-100: #3c404314;
$surface-200: #00000098;
$surface-300: #ffffff;
$surface-400: #f7f9fc;

nav {
 display: block;
 block-size: 4em;
 inline-size: 100%;
 position: sticky;
 top: 0;
 left: 0;
 background: $surface-300;
 transition: box-shadow .0875s linear;
 z-index: 1000;

 .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  block-size: 100%;
 }

 &.scrolled {
  box-shadow: 0 0 transparent, 0 0 transparent, 0 1px 3px 0 rgba(0,0,0,.1), 0 1px 2px -1px rgba(0,0,0,.1);
 }

 &.nav-mobile {
  .container {
   padding-inline: .75em;

   & > a {
    display: inline-flex;
    align-items: center;
    gap: .25em;
    font-family: $font-100;
    color: $text-100;
    position: relative;
    letter-spacing: .03em;
    text-transform: uppercase;

    strong {
     font-weight: 600;
     color: $text-200;
    }
   }

   button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: .625em;
    block-size: 3.25em;
    inline-size: 3.25em;
    border-radius: 50%;
    transition: background-color 100ms linear;

    &:hover {
     background-color: $surface-100;
    }
   }
  }

  aside {
   position: fixed;
   top: 0;
   left: -260px;
   inline-size: 260px;
   block-size: 100%;
   overflow: hidden;
   transition: all 200ms ease 0s;
   z-index: 1000;

   &.expanded {
    left: 0px;

    .scrim {
     visibility: visible;
    }
   }

   .scrim {
    position: fixed;
    top: 0;
    left: 0;
    block-size: 100%;
    inline-size: 100%;
    overscroll-behavior: contain;
    pointer-events: inherit;
    visibility: hidden;
    z-index: 1000;
    background: $surface-200;
    -webkit-tap-highlight-color: transparent;

    &::after {
     content: "";
     display: block;
     block-size: 200%;
     inline-size: 200%;
    }
   }

   .drawer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1.25rem;
    position: relative;
    block-size: 100%;
    inline-size: 100%;
    padding-block: 1em;
    background: $surface-400;
    z-index: 1001;

    & > a {
     display: inline-flex;
     align-items: center;
     justify-content: center;
     text-align: center;
     gap: .25em;
     font-family: $font-100;
     color: $text-100;
     padding-block: .25em;
     position: relative;
     letter-spacing: .03em;
     text-transform: uppercase;
 
     strong {
      font-weight: 600;
      color: $text-200;
     }
    }

    figure {
     display: flex;
     block-size: 140px;
     inline-size: 140px;
     overflow: hidden;
     border-radius: 50%;
     margin-inline: auto;

     img {
      display: block;
      object-fit: cover;
      border-radius: inherit;
     }
    }

    ul {
     &:nth-of-type(1) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      flex: 1;

      li {
       display: inline-flex;

       a {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-family: $font-100;
        color: $text-100;
        position: relative;
        text-align: center;
        letter-spacing: .01em;
        font-size: 1.25em;

        &.active {
         color: $text-200;
        }
       }
      }
     }

     &:nth-of-type(2) {
      display: flex;
      align-items: center;
      justify-content: space-around;

      li {
       display: inline-flex;

       a {
        display: inline-flex;

        svg {
         fill: $text-300;
        }

        &:hover,
        &:active {
         svg {
          fill: $text-200;
         }
        }
       }
      }
     }
    }
   }
  }
 }

 &.nav-desktop {
  .container {
   padding-inline: 3.5em;

   & > a {
    display: inline-flex;
    align-items: center;
    gap: .25em;
    font-family: $font-100;
    color: $text-100;
    position: relative;
    letter-spacing: .03em;
    text-transform: uppercase;

    strong {
     font-weight: 600;
     color: $text-200;
    }
   }

   ul {
    display: flex;
    align-items: center;
    gap: 1.5em;
    block-size: 100%;

    li {
     display: inline-flex;
     block-size: 100%;

     a {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      font-family: $font-100;
      color: $text-100;
      position: relative;
      text-align: center;
      letter-spacing: .01em;
      font-size: 1em;
      block-size: 100%;

      &::before {
       content: "";
       position: absolute;
       block-size: .125em;
       inline-size: 100%;
       bottom: 0;
       left: 0;
       border-radius: .125em .125em 0 0;
       background: transparent;
      }

      &.active {
       color: $text-200;

       &::before {
        background: $text-200;
       }
      }
     }
    }
   }
  }
 }
}