$text-100: #585b63;
$text-200: #4f68f1;
$surface-100: #4f68f1;
$surface-200: #f7f9fc;
$border-100: #585b63;
$border-200: #4f68f1;

footer {
 display: block;
 padding-block: 3em;
 background: $surface-200;

 .container {
  padding-inline: .75em;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;

  & > a {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   max-inline-size: 300px;
   inline-size: 100%;

   img {
    display: block;
    object-fit: cover;
    filter: invert(.75);
   }
  }

  ul {
   display: flex;
   align-items: center;
   justify-content: space-between;
   max-inline-size: 300px;
   inline-size: 100%;

   li {
    display: inline-flex;

    a {
     display: inline-flex;
     align-items: center;
     justify-content: center;
     position: relative;
     block-size: 3em;
     inline-size: 3em;
     border-radius: 50%;
     border: 1px solid $border-100;
     user-select: none;
     transition: background-color 150ms linear, border-color 150ms linear;
     -webkit-user-select: none;

     svg {
      fill: $text-100;
     }

     &:hover,
     &:active {
      background-color: $surface-100;
      border-color: $border-200;

      svg {
       fill: $surface-200;
      }
     }
    }
   }
  }

  p {
   font-size: .875em;
  }
 }

 @media screen and (min-width: 768px) {
  .container {
   padding-inline: 3.5em;
  }
 }
}