$text-100: #fcfbfb;

.media-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 inline-size: 100%;
 margin-block: 3em;

 .media-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  border-radius: .75em;
  overflow: hidden;
  box-shadow: 0 0 0 1px rgba(0,0,0,.05);

  .media-card-image {
   display: flex;
   position: relative;
   border-radius: inherit;

   img {
    display: block;
    object-fit: cover;
    border-radius: inherit;
    transition: all .25s ease-in-out;
   }
  }

  .media-card-details {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   gap: .5em;
   border-radius: inherit;
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65));
   position: absolute;
   block-size: 100%;
   inline-size: 100%;
   z-index: 10;

   h4 {
    font-size: 1.25em;
    font-weight: 500;
    letter-spacing: .02em;
    text-shadow: 0 1px 0 #000;
    color: $text-100;
    margin-block-start: 1em;
    -webkit-font-smoothing: antialiased;
   }

   svg {
    fill: $text-100;
   }
  }

  &:hover {
   .media-card-image {
    img {
     transform: scale(1.2);
    }
   }
  }
 }

 @media screen and (min-width: 600px) {
  grid-template-columns: repeat(2, 1fr);
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr);
 }
}