$font-100: "M PLUS 1", sans-serif;
$text-100: #4f68f1;
$text-200: #f7f9fc;
$surface-100: #f7f9fc;
$surface-200: #4f68f1;
$border-100: #4f68f1;

.outlined-btn {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 gap: .325em;
 margin-inline: auto;
 text-align: center;
 font-size: 1.1875em;
 font-weight: 500;
 line-height: 1.5;
 color: $text-100;
 border-radius: .5em;
 padding: .875em 1.25em;
 background: transparent;
 border: 1px solid $border-100;
 user-select: none;
 transition: background-color 75ms linear;
 -webkit-font-smoothing: antialiased;
 -webkit-user-select: none;

 svg {
  position: relative;
  top: 1px
 }

 &:hover,
 &:active {
  background-color: $surface-200;
  color: $text-200;

  svg {
   fill: $text-200;
  }
 }
}